import React from "react";
import "./About.css";
import aboutImg from "./img/about-1.jpg";

function About() {
  // Up to Top Btn
  window.addEventListener("scroll", function () {
    const upToTop = document.querySelector(".bottom__to__top");
    upToTop.classList.toggle("active", window.scrollY > 0);
  });
  return (
    <div className="about component__space" id="About">
      <div className="container">
        <div className="row">
          <h1 className="about__heading black">About Me</h1>
        </div>
        <div className="row">
          <div className="col__2">
            <img src={aboutImg} alt="" className="about__img" />
          </div>

          <div className="col__2">
            <p className="about__text p__color black">
              An innovative individual pursing a BTech in Information and
              Communication Technology Engineering, desirous to find the job
              position of a Software Engineer to apply my exceptional knowledge
              of design, development, coding and quality testing of software as
              per clients requirements.<br></br>
              <br></br><strong>Technical Skills:</strong><br></br> Native Android Developement
              <br></br> React JS<br></br> Node JS<br></br> Java<br></br> Kotlin<br></br>
              Python<br></br> C++<br></br> C
            </p>
          </div>
        </div>
      </div>
      {/* UP TO TOP BTN*/}
      <div className="up__to__top__btn">
        <a href="#" className="bottom__to__top">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="16"
            height="16"
            fill="currentColor"
            class="bi bi-chevron-up"
            viewBox="0 0 16 16"
          >
            <path
              fill-rule="evenodd"
              d="M7.646 4.646a.5.5 0 0 1 .708 0l6 6a.5.5 0 0 1-.708.708L8 5.707l-5.646 5.647a.5.5 0 0 1-.708-.708l6-6z"
            />
          </svg>
        </a>
      </div>
    </div>
  );
}

export default About;
