import {useEffect, useRef } from "react";

function Map({
  center,
  zoom,
}) {
  const ref = useRef();
  const style = { height: "450px" };
  useEffect(() => {
    new window.google.maps.Map(ref.current, {
      center: { lat: 21.207374044620778, lng: 72.83905349721722 },
      zoom: 17,
    });
  });
  return (
    <div ref={ref} style={style} id="map">
    </div>
  );
}

export default Map;
