import React from "react";
import "./Mission.css";
import MissionImg from "./img/missionImg-2.png";
import Vision from "./img/vision-2.jpg";
import Motto from "./img/motto.jpg";

function Mission() {
  return (
    <div className="mission component__space" id="Mission">
      <div className="container">
        <div className="row">
          <h1 className="mission__heading black">My Ambitions</h1>
        </div>

        <div className="row">

          <div className="col__3">
            <div class="flip-box">
              <div class="flip-box-inner">
                <div class="flip-box-front">
                  <h1 className="pq">MISSION</h1>
                </div>
                <div class="flip-box-back">
                  <h2 className="rs">To enrich the world with technology</h2>
                </div>
              </div>
            </div>
          </div>

          <div className="col__3">
            <div class="flip-box">
              <div class="flip-box-inner">
                <div class="flip-box-front">
                  <h1 className="pq">VISION</h1>
                </div>
                <div class="flip-box-back">
                  <h2 className="rs">I see the world with technological aspect and want to ease
                    the lifestyle with technology</h2>
                </div>
              </div>
            </div>
          </div>

          <div className="col__3">
            <div class="flip-box">
              <div class="flip-box-inner">
                <div class="flip-box-front">
                  <h1 className="pq">MESSAGE</h1>
                </div>
                <div class="flip-box-back">
                  <h2 className="rs">Your Problems...<br></br>My Solution...</h2>
                </div>
              </div>
            </div>
          </div>

        </div>
      </div>
    </div>
  );
}

export default Mission;
