import React, { useState } from "react";
import "./Contact.css";
import Map from "./Map";
import contactImg from "./img/about-9.jpg";
import { withGoogleMap, GoogleMap } from "react-google-maps";
import { Wrapper, Status } from "@googlemaps/react-wrapper";
import axios from 'axios';
import swal from 'sweetalert';

function Contact() {
  
  const center = { lat: 21.207374044620778, lng: 72.83905349721722 };
  const zoom = 17;

  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [mobile, setMobile] = useState('');
  const [message, setMessage] = useState('');
 

  const handleSubmit=(e)=>{
    e.preventDefault();
    const data={
      Name: name,
      Email: email,
      Mobile: mobile,
      Message: message
    }

    axios.post('https://sheet.best/api/sheets/ae42e128-10e6-4618-b3c7-5309fe9e27f1', data).then((response)=> {
      setName('');
      setEmail('');
      setMobile('');
      setMessage('');
    })
    
    window.onload = setTimeout(function(){
      swal({
        title: "Thank You!",
        text: "Message sent successfully!",
        icon: "success",
      });
  }, 1500);

    
  }


  return (
    <div className="contact component__space" id="Contact">
      <div className="heading">
        <h1 className="heading black">Contact Me</h1>
        <p className="heading p__color black">
          I am available for freelance work. Connect with me via email/mobile:
        </p>
        <p className="heading p__color black">
          <strong>meetsheladiya7777@gmail.com/+91 99044 87777</strong>
        </p>
      </div>
      <div className="container">
        <div className="row2">
          <div className="col__2__custom">
            <div className="contact__box">
              <div className="input__box">
                <form autoComplete="off" className="form-group" onSubmit={handleSubmit}>
                  <input
                    type="text"
                    className="contact name"
                    placeholder="Your Name *"
                    required
                    onChange={(e) => setName(e.target.value)} value={name}
                  />
                  <input
                    type="text"
                    className="contact email"
                    placeholder="Your Email *"
                    required
                    onChange={(e) => setEmail(e.target.value)} value={email}
                  />
                  <input
                    type="text"
                    className="contact phone"
                    placeholder="Your Mobile Number *"
                    required
                    onChange={(e) => setMobile(e.target.value)} value={mobile}
                  />
                  <textarea
                    id="message"
                    placeholder="Write your message"
                    onChange={(e) => setMessage(e.target.value)} value={message}
                  ></textarea>
                  <h2 className="ba">* indicates required fields</h2>
                  <button
                    className="btn contact pointer"
                    type="submit"
                  >
                    Submit
                  </button>
                </form>
              </div>
            </div>
          </div>

          <div className="col__2__cust">
            
            <Wrapper apiKey={"AIzaSyA6SgRveSBCbwtn4zPqwX_mt_KF-PEg1yk"}>
                <Map center={center} zoom={zoom}>
                </Map>
            </Wrapper>
            
          </div>
        </div>
      </div>
    </div>
  );
}

export default Contact;
