import React from 'react'
import footerImg from './img/logo-3.png';
import "./Footer.css";
function Footer() {
  return (
    <div className="footer d__flex align__items__center justify__content__space__between pz-10" style={{"padding":"10px 20px","zIndex":"1000"}}> 
      <img src={footerImg} alt="" className="footer__img pointer" />
      <span className="copyright" style={{"color":"#fff","fontSize":"14px","opacity":"0.75"}}>Copyright  Meet Sheladiya. All Rights Reserved</span>
    </div>
  )
}

export default Footer
